.chat {
    &__wrapper {
        background-color: #e0e0e0;// darken($color-secondary, 8%);
        border-top: 1px solid #c0c0c0;
        max-width: 500px;
        padding: .5em 0 .5em .5em;
    }

    &__log {
        border: 1px solid #999;
        border-radius: .25em;
        height: 33vh;
        list-style: none;
        padding: .5em .5em 2em;
        position:relative;
        overflow: scroll;
    }


    &__typing-indicator {
        bottom: 0;
        font-size: .9em;
        font-style: italic;
        position: absolute;
    }

    &__input {
        align-items: center;
        display: flex;
        position: relative;

        &__send-button {
            background-color: #e0e0e0;
            border: none;
            color: darken($color-primary, 20%);
            cursor: pointer;
            width: 5em;

            &[disabled] {
                color: #666;
                cursor: default;
            }
        }

        &__field {
            border: none;
            //border-right: solid .5em #e0e0e0;
            flex-grow: 1;
            font-size: .875em;
            margin-right: .25em;
            padding: .25rem .5rem;
        }
    }
}
