*,
*:after,
*:before {
	box-sizing: border-box;
}

body,
html {
	height: 100%;
    padding: 0;
    margin: 0;
}

html {
	background-color: #fff;
}

body {
	-webkit-text-size-adjust: 100%;
    color: #2b2b2b;
	display: flex;
	flex-direction: column;
    font: 16px/1.5 Arial, sans-serif;
	margin: auto;
	max-width: 500px;
	overflow: hidden;
}

button,
input {
    font: inherit;
}
